<template>
  <div class="make">
    <div class="top">
      <a @click="goBack">
        <i></i>
      </a>
      <h3>猫眼电影</h3>
    </div>
    <div class="inp">
      <div class="seach">
        <i></i>
        <input type="text" placeholder="搜电影，搜影院" v-model="message">
      </div>
      <label>取消</label>
    </div>
    <div class="seachResult">
       <FilmItme v-for="(item, index) in allmessage" :key="index" v-bind:item="item"></FilmItme>
    </div>
   
  </div>
</template>
<style lang="less" scoped>
.make {
  .top {
    height: 50px;
    background-color: #e54847;
    display: flex;
    align-items: center;
    a {
      flex-grow: 1;
      i {
        margin-left: 20px;
        display: inline-block;
        line-height: 50px;
        width: 16px;
        height: 16px;
        border-bottom: 2px solid #ffffff;
        border-left: 2px solid #ffffff;
        transform: rotate(45deg);
      }
    }
    h3 {
      margin: 0;
      font-size: 18px;
      font-weight: normal;
      color: white;
      line-height: 50px;
      flex-grow: 1;
    }
  }
  .inp {
    background: #f5f5f5;
    height: 50px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 10px;

    .seach {
          flex-grow: 2;
      background: white;
      height: 30px;
      border-radius: 5px;
      border-bottom: 1px solid #e5e5e5;
      i {
        margin-left: 10px;
        display: inline-block;
        width: 14px;
        height: 14px;
        background-image: url(data:image/png;base64,);
        background-size: 100% 100%;
      }
      input {
        margin-left: 10px;
        border-radius: 5px;
        color: #777;
        border: none;
        outline: none;
      }
    }
    label{
       flex-grow: 1;
      color:#e54847;
      text-align: center;
    }
  }
  .seachResult{
    height: 100%;

  }
}
</style>
<script>
import FilmItme from "../components/FilmItme";
export default {
  name: "make",
  components: {
    FilmItme
  },
  data() {
    return {
      message: null,
      allmessage: null,
      click: true,
      time: new Date()
    };
  },
  methods: {
    goBack: function() {
      this.$router.go(-1);
    },

    change: function() {
      var self = this;
      this.click = false;
      setTimeout(() => {
        self.click = true;
      });
      this.$axios
        .get("http://api.kele8.cn/agent/http://m.maoyan.com/ajax/search?", {
          params: {
            kw: self.message,
            cityId: 30,
            stype: -1
          }
        })
        .then(function(result) {
          console.log(result.data.movies);
          self.allmessage = result.data.movies.list;
          self.allmessage.forEach(element => {
            var imgUrl =
              "https://p1.meituan.net/128.180/movie/" +
              element.img.split("/")[5];
            element.img = imgUrl;
          });
        });
    }
  },
  watch: {
    message: function() {
      if (this.click) {
        this.change();
      }
    }
  },
  created() {
    let year = this.time.getFullYear();
    let mouth = this.time.getMonth() + 1;
    let data = this.time.getDate();
    mouth = mouth < 10 ? "0" + mouth : mouth;
    data = data < 10 ? "0" + data : data;
    this.time = `${year}-${mouth}-${data}`;
    //  console.log(this.item);
  }
};
</script>


